<template>
  <div class="box">
    <div class="css-custom">
      <h1>TXT 파일 처리</h1>
      <input
        type="file"
        @change="handleFileUpload"
        accept=".txt"
        class="txt-button"
      />
      <!-- 라디오 버튼 추가 -->
      <h1>시나리오 선택</h1>
      <div class="theme-selection">
        <label>
          <input
            type="radio"
            value="bloodlord"
            v-model="selectedTheme"
            @change="updateThemeCSS"
          />
          블러드로드
        </label>
        <label>
          <input
            type="radio"
            value="carrioncrown"
            v-model="selectedTheme"
            @change="updateThemeCSS"
          />
          케리온크라운
        </label>
      </div>
      <h1>캐릭터 이미지 업로드</h1>
      <div
        v-for="(character, index) in characters"
        :key="index"
        class="character-input"
        :class="{ dimmed: ignoredCharacters.has(character) }"
      >
        <label :for="`file-${character}`">{{ character }} 이미지 업로드:</label>
        <div class="image-upload">
          <input
            v-if="!ignoredCharacters.has(character)"
            type="file"
            :id="`file-${character}`"
            accept="image/*"
            @change="handleImageUpload($event, character)"
          />
          <button
            v-if="!ignoredCharacters.has(character)"
            @click="ignoreCharacterImage(character)"
          >
            무시하기
          </button>
          <button v-else @click="restoreCharacterImage(character)">
            복원하기
          </button>
        </div>
      </div>
      <textarea
        v-if="generatedCSS"
        v-model="generatedCSS"
        rows="10"
        cols="50"
        readonly
      ></textarea>

      <textarea
        v-if="processedContent"
        v-model="processedContent"
        rows="10"
        cols="50"
        readonly
      ></textarea>
      <input
        type="text"
        v-model="customTitle"
        placeholder="출력 제목을 입력하세요"
      />
      <button
        @click="generateAndPreview"
        :disabled="!rawContent"
        class="custom-button"
      >
        출력 및 CSS 생성
      </button>

      <!-- 결과 다운로드 버튼 -->
      <button
        @click="downloadResult"
        :disabled="!processedContent"
        class="custom-button"
      >
        결과 다운로드
      </button>
    </div>
  </div>
</template>
<script>
export default {
  name: "HomeView",
  data() {
    return {
      rawContent: "", // 업로드된 원본 텍스트
      processedContent: "", // 처리된 텍스트
      customTitle: "", // 사용자 정의 제목
      characters: [], // 전체 캐릭터 목록
      excludedCharacters: [
        "Gamemaster",
        "01-GM",
        "나레이터",
        "나기",
        "닐로스",
        "타츠",
        "안테노르",
        "바쉬라",
        //-----------
        "에드나",
        "스트랄",
        "에버 그린",
        "에버그린",
        "세반",
        "스트랄 레인하트",
        "지그문트",
        "에드나 크로브나",
      ], // 제외할 캐릭터 목록
      characterImages: {}, // 각 캐릭터의 base64 이미지 저장
      ignoredCharacters: new Set(), // 무시된 캐릭터 목록
      generatedCSS: "", // 생성된 CSS
      selectedTheme: "", // 선택된 시나리오 테마
    };
  },
  computed: {
    filteredCharacters() {
      // 제외된 캐릭터와 무시된 캐릭터를 필터링한 목록 반환
      return this.characters.filter(
        (character) =>
          !this.excludedCharacters.includes(character) &&
          !this.ignoredCharacters.has(character)
      );
    },
  },
  methods: {
    // 통합된 메서드
    generateAndPreview() {
      if (!this.rawContent) {
        console.error("rawContent가 비어 있습니다.");
        return;
      }

      // CSS 생성
      this.generateCSS();

      // HTML 출력
      this.processedContent = this.convertToHTML(this.rawContent);
    },

    handleFileUpload(event) {
      const file = event.target.files[0];
      if (file) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.rawContent = e.target.result; // 원본 텍스트 저장
          this.extractCharacters(); // 캐릭터 이름 추출
        };
        reader.readAsText(file);
      }
    },
    ignoreCharacterImage(character) {
      // 무시된 캐릭터 목록에 추가
      this.ignoredCharacters.add(character);

      // 해당 캐릭터의 이미지를 삭제
      if (this.characterImages[character]) {
        delete this.characterImages[character];
      }
    },
    restoreCharacterImage(character) {
      // 무시된 캐릭터 목록에서 제거
      this.ignoredCharacters.delete(character);
    },
    extractCharacters() {
      // rawContent에서 캐릭터 이름 추출
      const lines = this.rawContent.split("\n");
      const characterSet = new Set();

      lines.forEach((line) => {
        // 예: "[2/6/2025, 1:51:14 PM] 캐릭터이름" 형식에서 캐릭터 이름 추출
        const match = line.match(
          /^\[\d{1,2}\/\d{1,2}\/\d{4}, \d{1,2}:\d{2}:\d{2} (AM|PM)\] (.+)$/
        );
        if (match) {
          const characterName = match[2].trim();
          if (!this.excludedCharacters.includes(characterName)) {
            characterSet.add(characterName);
          }
        }
      });

      this.characters = Array.from(characterSet); // 중복 제거 후 배열로 변환
    },
    handleImageUpload(event, character) {
      const file = event.target.files[0];
      if (file) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.characterImages[character] = e.target.result; // base64 image
        };
        reader.readAsDataURL(file); // Read the file as a base64 data URL
      }
    },

    previewResult() {
      if (!this.rawContent) {
        console.error("rawContent가 비어 있습니다.");
        return;
      }
      // convertToHTML 호출 결과를 processedContent에 저장
      this.processedContent = this.convertToHTML(this.rawContent);
    },
    generateCSS() {
      const css = Object.entries(this.characterImages)
        .map(([character, base64Image]) => {
          const className = character
            .trim()
            .replace(/\s+/g, "-")
            .replace(/[^a-zA-Z0-9\uAC00-\uD7A3-_]/g, ""); // 한글 포함
          return `
        .author-${className} {
          background-image: url('${base64Image}');
        }
      `;
        })
        .join("\n");

      this.generatedCSS = css;
      // 테마 CSS 추가
      this.updateThemeCSS();
      "Generated CSS:", this.generatedCSS; // 디버깅용 로그
    },
    updateThemeCSS() {
      let themeCSS = "";
      if (this.selectedTheme === "bloodlord") {
        themeCSS = `
          .author-러츠GM {
  background-image: url('data:image/webp;base64,');
  background-size: cover;
  background-position: center;
  background-color: #222427;
  width: 48px;
  height: 48px;
  display: inline-block;
  border-radius: 50%;
  margin-right: 8px;
}
.author-System {
  background-image: url('data:image/webp;base64,');
  background-size: cover;
  background-position: center;
  background-color: #222427;
  width: 48px;
  height: 48px;
  display: inline-block;
  border-radius: 50%;
  margin-right: 8px;
}

.author-지그문트 {
  background-image: url('data:image/webp;base64,');
  background-size: cover;
  background-position: center;
  background-color: #222427;
  width: 48px;
  height: 48px;
  display: inline-block;
  border-radius: 50%;
  margin-right: 8px;
}

.author-스트랄 {
  background-image: url('data:image/webp;base64,');
  background-size: cover;
  background-position: center;
  background-color: #222427;
  width: 48px;
  height: 48px;
  display: inline-block;
  border-radius: 50%;
  margin-right: 8px;
}


